header {
	width: fit-content;
	color: #bf91f3;
}

.nav-item a {
	color: white;
}

@media (max-width: 991.99px) {
	.nav-item-active,
	.nav-item:hover {
		color: #e3ac3c !important;
	}
}

@media (min-width: 992px) {
	.nav-item-active,
	.nav-item:hover {
		border-bottom: 2px solid #e3ac3c;
	}
}

.c-primary {
	color: #e3ac3c;
	/* color: #e4d804; */
}

.border-color-primary {
	border-color: #e3ac3c !important;
}

.movie-poster {
	object-fit: cover;
	object-position: center center;
	transition: all 0.3s ease-in-out;
	/* cursor: pointer; */
}

.movie-poster:hover {
	transform: scale(1.1, 1.1);
	opacity: 0.85;
}

.form-select:focus {
	border-color: #e3ac3c;
	box-shadow: 0 0 0 0.25rem rgb(228, 216, 4, 0.25);
}

.c-sub-title {
	color: '#333';
}

button:disabled {
	cursor: not-allowed;
}

.page-title {
	position: relative;
}
.page-title::after {
	content: '';
	display: block;
	position: absolute;
	top: 130%;
	left: 50%;
	transform: translateX(-50%);
	padding: 1px;
	width: 80px;
	background-color: white;
}

.navbar-list {
	/* display: flex; */
}

@media (max-width: 991.99px) {
	.navbar-list {
		max-height: 0;
		overflow: hidden;
		transition: 0.6s;
	}

	.navbar-list.show {
		margin-top: 15px;
		max-height: 100vh;
		/* overflow: visible; */
	}
}

/* remove scrollbar in chrome */
/* html::-webkit-scrollbar {
	display: none;
} */

/* remove scrollbar in firefox */
/* html {
	scrollbar-width: none;
} */

::-webkit-scrollbar {
	width: 9px; /* Mostly for vertical scrollbars */
	height: 9px; /* Mostly for horizontal scrollbars */
}

::-webkit-scrollbar-track {
	/* Foreground */
	padding: 10px;
	background-color: #e1e1e1;
	box-shadow: 0 0 3px #999 inset;
}

::-webkit-scrollbar-thumb {
	/* Background */
	box-shadow: 0 0 3px 1px #666 inset;
	background-color: #888;
	/* border-radius: 3px; */
	transition: all 0.5s;
}

::-webkit-scrollbar-thumb:hover {
	box-shadow: 0 0 3px 1px #888 inset;
	background-color: #666;
}
